import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { EV_CONTACT_PROP, EVContact } from "../type/event-report-type";
import { BXContact } from "@/app/types/bitrix/bitrix-type";


//TYPES
export type EventContactState = typeof initialState


export interface SetFetchedEventContact {
    contacts: BXContact[],
}

export interface SetCurrentEventContact {
    contactId: number | '',
}

const initialState = {

    contacts: [] as BXContact[],
    current: {
        contact: null as null | undefined | BXContact,
        // default: null as null | EVContact,
        type: null as 'new' | 'updating' | 'existing' | null
    },
    isFetched: false as boolean,
    isLoading: false as boolean,
    // errors: {
    //     fetching: [] as string[],
    // }
}


const eventContactSlice = createSlice({
    name: 'eventContactSlice',
    initialState,
    reducers: {

        setFetchedContacts: (
            state: EventContactState,
            action: PayloadAction<SetFetchedEventContact>

        ) => {

            const pay = action.payload
            state.contacts = pay.contacts
            if (pay.contacts.length) {
                state.current.contact = null
            } else {
                state.current.contact = null
            }
            state.isFetched = true
            state.isLoading = false

        },
        setCurrentContact: (
            state: EventContactState,
            action: PayloadAction<SetCurrentEventContact>

        ) => {

            const pay = action.payload
            if (pay.contactId) {
                state.current.contact = state.contacts.find((contact) => contact.ID == pay.contactId)
            } else {
                state.current.contact = null
            }
            

        },

    },

});


//utils


export const eventContactReducer = eventContactSlice.reducer;

//  actions
export const eventContactActions = eventContactSlice.actions;