import { FC, useState } from "react"

import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import ASelect from "@/shared/ui/Inputs/Select/ASelect"
import { eventContactActions } from "../model/EventContactSlice"




export const EventContact: FC = ({


}) => {

    const contacts = useAppSelector(state => state.contact.contacts)
    const current = useAppSelector(state => state.contact.current.contact)
    const dispatch = useAppDispatch();
    const setCurrent = (
        type: 'contact',
        contactId: string | number
    ) => {
        const id = contactId as number
        dispatch(
            eventContactActions
                .setCurrentContact({
                    contactId: id
                })
        )
    }
    // return <EVCard
    //     title=""
    //     width={12}
    //     children={
    //         <div>
    //             
    //             <ASelect
    //                 current={current}
    //                 items={contacts}
    //                 withLabel
    //                 label=""
    //                 nameForHandler="contact"
    //                 handleChange={() => alert('contact')}
    //             />
    //         </div>
    //     }
    //     size="medium"
    //     key={1}
    // />
    return <ASelect
        current={current}
        items={contacts}
        withLabel
        label=""
        nameForHandler="contact"
        handleChange={setCurrent}
    />
}

