import { AppDispatch, AppGetState } from "@/app/model/store";
import { bitrixAPI } from "@/app/services/bitrix-general-api";
import { eventContactActions } from "./EventContactSlice";

export const getCompanyContacts = (
    // bxCompany: BXCompany, 
    companyId: number
) => async (
    dispatch: AppDispatch, getState: AppGetState
) => {


        const contactsFrom = await bitrixAPI.getMethod(
            'crm.company.contact.items.get',
            {
                id: companyId

                // SELECT: ['NAME', 'PHONE', 'ID']
            },
            'april-dev.bitrix24.ru'
        );
        //@ts-ignore
        const contactIds = contactsFrom.map(contact => contact.CONTACT_ID)

        const contacts = await bitrixAPI.getMethod(
            'crm.contact.list',
            {
                filter: {
                    'ID': contactIds
                },
                // SELECT: ['NAME', 'PHONE', 'ID']
            },
            'april-dev.bitrix24.ru'
        );
        debugger
        dispatch(
            eventContactActions
                .setFetchedContacts({
                    contacts
                })
        )
        // if (contacts && contacts.length) {

        //     const contactId = contacts[0]['CONTACT_ID']
        //     const contact = await bitrixAPI.getMethod(
        //         'crm.company.contact.items.get',
        //         { id: contactId, },
        //         'april-dev.bitrix24.ru'
        //     );
        // }
        debugger
    }